export default {
    props: {
        name: {
            type: String,
            value: ""
        },
        examitemid: {
            type: String,
            value: ""
        },
        wordtype: {
            type: String,
            value: ""
        },
        mid: {
            type: String,
            value: ""
        },
    },
    data() {
        return {
            animationData: {},
            showDailoge: false
        }
    },
    created() {
        this.animation = {};
    },
    methods: {
        show() {
            this.$util.modalAnimationFunc(this, "open", "animation", "animationData", "showDailoge");
        },
        hide() {
            this.$util.modalAnimationFunc(this, "close", "animation", "animationData", "showDailoge");
        },

        clickBtn(type){
            console.log('this.examitemid',this.examitemid)
            // return;
            var url;
            if(type=='vocab'){
                url = `${this.$path.toefl.word.listenSetUrl}?name=${this.name}&aeid=0&id=${this.examitemid}&type=${this.wordtype}&mid=${this.mid}`
            }else{
                url = `${this.$path.toefl.word.reciteSetUrl}?name=${this.name}&id=${this.examitemid}&type=${this.wordtype}&mid=${this.mid}`
            }
            uni.navigateTo({
                url:url
            });
            this.hide();
        },
    }
};
